* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #d3d3d3;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

.logo {
  max-width: 150px;
  height: auto;
  margin: 0 auto 10px;
  display: block;
}

header {
  text-align: center;
  color: #fff;
  z-index: 1;
  background-image: url("https://res.cloudinary.com/dvcust3vi/image/upload/v1727355135/gif_q5zrdx.gif");
  background-position: center;
  background-size: cover;
  padding: 20px;
  position: relative;
}

header:before {
  content: "";
  z-index: -1;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.heading {
  font-family: Times New Roman, Times, serif;
  font-size: 48px;
}

.section-title {
  text-align: center;
  margin: 1rem 0;
  font-size: 24px;
}

.nav {
  margin-top: 2rem;
}

.nav-links {
  justify-content: center;
  gap: 4rem;
  list-style: none;
  display: flex;
}

.nav-links.active {
  z-index: 1000;
  background-color: #2c3e50;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.nav-button {
  color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 20px;
  font-size: 2rem;
  text-decoration: none;
}

.nav-button:hover {
  background-color: #34495e;
  box-shadow: 0 3px 10px #fff;
}

.hamburger {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 30px;
  display: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-button {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  margin: 10px;
  font-size: 30px;
  display: none;
}

main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.section-container {
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  padding: 2rem;
  display: grid;
}

.article-item {
  text-align: left;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  flex-direction: column;
  padding: 1rem;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

.article-item:hover {
  transform: scale(1.02);
}

.article-item img {
  border-radius: 4px;
  max-width: 100%;
  height: auto;
  margin-bottom: 1rem;
}

.article-content {
  padding: 1rem 0;
}

.article-content h3 {
  text-align: center;
  color: #000;
  margin-bottom: .5rem;
  font-size: 1.2rem;
}

.article-content p {
  color: #555;
  margin-bottom: 1rem;
  font-size: .9rem;
}

.article-content a {
  color: #fff;
  background-color: #0074cc;
  border-radius: 4px;
  padding: 10px 15px;
  font-size: 1rem;
  text-decoration: none;
  display: inline-block;
}

.article-content a:hover {
  background-color: #005999;
}

footer {
  text-align: center;
  color: #fff;
  background-color: #2c3e50;
  padding: 1rem;
  font-size: .9rem;
}

@media (width <= 768px) {
  .section-container {
    grid-template-columns: repeat(1, 1fr);
  }

  .nav-links {
    display: none;
  }

  .nav-links.active {
    display: flex;
  }

  .hamburger, .close-button {
    display: block;
  }
}
